import dayjs, { type Dayjs } from 'dayjs'
import { useSnackbar } from 'notistack'
import { type ChangeEvent, useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Container,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'
import { Card } from '@mui/material'
import type { Editor } from '@toast-ui/react-editor'

import { COMMON_MESSAGE, CONFERENCE_MESSAGE } from 'src/data/messages'

import { type ClubMember } from 'src/models/ClubMember'
import type { DateProps } from 'src/models/date'
import { defaultDate } from 'src/models/date'

import PageTitleWrapper from 'src/components/PageTitleWrapper'

import ConferenceInfo from 'src/content/manage/Conference/ConferenceEdit/ConferenceInfo'
import ConferenceNote from 'src/content/manage/Conference/ConferenceEdit/ConferenceNote'
import PageHeader from 'src/content/manage/PageHeader'

import { useEditConference, useGetSingleConference } from 'src/hooks/api/clubConferenceHooks'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

function ConferenceEdit() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('md'))
  const { enqueueSnackbar } = useSnackbar()
  const { userInfo } = useSafeAuthUser()
  const editorRef = useRef<Editor>(null)
  const { id, confId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)

  const club = userInfo?.clubs.find(club => club.id === clubId)
  const clubDate: DateProps = { year: club?.year || defaultDate.year, semester: club?.semester || defaultDate.semester }

  const { data, isSuccess } = useGetSingleConference(clubId, clubDate, conferenceId)
  const editConferenceMutation = useEditConference()

  const [title, setTitle] = useState<string>('')
  const [startDate, setStartDate] = useState<Dayjs | null>(null)
  const [selectedMembers, setSelectedMembers] = useState<number[]>(
    isSuccess ? data?.member.map(member => member.id) : []
  )

  useEffect(() => {
    if (isSuccess && data) {
      setTitle(data.title)
      setStartDate(dayjs(data.startAt))
    }
  }, [isSuccess, data])

  const changeTitle = (title: string) => {
    setTitle(title)
  }

  const changeStartDate = (startDate: Dayjs | null) => {
    setStartDate(startDate)
  }

  const selectAllMembers = (event: ChangeEvent<HTMLInputElement>, members: ClubMember[]) => {
    setSelectedMembers(event.target.checked ? members && members.map(member => member.id) : [])
  }

  const selectOneMember = (memberId: number) => {
    setSelectedMembers(prevSelected => [...prevSelected, memberId])
  }

  const deleteOneMember = (memberId: number) => {
    setSelectedMembers(prevSelected => prevSelected.filter(id => id !== memberId))
  }

  const selectAllLevelMember = (levelMembers: number[]) => {
    setSelectedMembers(prevSelected => Array.from(new Set([...prevSelected, ...levelMembers])))
  }

  const deleteAllLevelMember = (levelMembers: number[]) => {
    setSelectedMembers(prevSelected => prevSelected.filter(memberId => !levelMembers.includes(memberId)))
  }

  const handleSubmit = async () => {
    if (title.length > 50) {
      enqueueSnackbar(CONFERENCE_MESSAGE.TITLE_LIMIT, { variant: 'error' })
      return
    }
    if (isSuccess) {
      editConferenceMutation.mutate({
        conferenceId: data.id,
        startAt: startDate,
        users: selectedMembers,
        note: editorRef.current?.getInstance().getHTML() || '',
        clubId,
        title,
        clubDate
      })
      return
    }
    enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
    return
  }

  return (
    <>
      <Helmet>
        <title>회의 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} buttonText="행사 추가" link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container>
        <Card>
          <Accordion square={!matches}>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel-content" id="panel-header">
              <Typography variant="h4" component="h4">
                희의록 작성
              </Typography>
            </AccordionSummary>
            <AccordionDetails id="panel-content">
              <ConferenceNote log={data?.log} note={data?.note} editorRef={editorRef} />
            </AccordionDetails>
          </Accordion>
          <Accordion defaultExpanded={true} square={!matches}>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel-content" id="panel-header">
              <Typography variant="h4" component="h4">
                참여자 및 회의 정보 수정
              </Typography>
            </AccordionSummary>
            <AccordionDetails id="panel-content">
              <ConferenceInfo
                title={title}
                changeTitle={changeTitle}
                date={startDate}
                changeDate={changeStartDate}
                selectedMembers={selectedMembers}
                selectAllMembers={selectAllMembers}
                selectOneMember={selectOneMember}
                deleteOneMember={deleteOneMember}
                selectAllLevelMember={selectAllLevelMember}
                deleteAllLevelMember={deleteAllLevelMember}
                handleSubmit={handleSubmit}
              />
            </AccordionDetails>
          </Accordion>
        </Card>
      </Container>
    </>
  )
}

export default ConferenceEdit
