import { useEffect } from 'react'

import { Box, Card, Typography } from '@mui/material'
import { type Editor } from '@toast-ui/react-editor'

import TextEditor from 'src/components/TextEditor'

const MAX_LOG_LENGTH = 31

interface ConferenceNoteProps {
  note: string | undefined
  log: string | undefined
  editorRef: React.RefObject<Editor>
}

const ConferenceNote: React.FC<ConferenceNoteProps> = ({ note = '', log = '', editorRef }) => {
  useEffect(() => {
    editorRef.current?.getInstance().setHTML(note)
  }, [])

  return (
    <Box paddingX={{ xs: 3, md: 6 }}>
      <Card>
        <TextEditor
          initialValue=""
          editorRef={editorRef}
          toolbarItems={[
            ['heading', 'bold', 'italic', 'strike'],
            ['hr', 'quote'],
            ['ul', 'ol', 'task'],
            ['table', 'link'],
            ['code', 'codeblock']
          ]}
        />
      </Card>
      <Box display="flex" justifyContent="right" sx={{ mr: 1, mt: 1 }}>
        <Typography variant="caption">{log.length > MAX_LOG_LENGTH && log}</Typography>
      </Box>
    </Box>
  )
}

export default ConferenceNote
